body {
  background-color: #d0d9da;
}

.form {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: Arial, sans-serif;
  display: flex;
}

label {
  flex-direction: column;
  margin-bottom: 20px;
  display: flex;
}

input[type="number"] {
  border: none;
  border-radius: 5px;
  padding: 10px;
  font-size: 16px;
  box-shadow: 0 0 5px #0000001a;
}

button[type="submit"] {
  color: #fff;
  cursor: pointer;
  background-color: #0074d9;
  border: none;
  border-radius: 5px;
  padding: 10px;
  font-size: 16px;
}

button[type="submit"]:hover {
  background-color: #004c8c;
}

